@charset "uft-8";
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');


body{
    font-family: 'Noto Sans KR', sans-serif;
}

*{margin: 0px; padding: 0px;}

#top{
    position: fixed;
    bottom: 30px;
    right: 30px;
    cursor: pointer;
    box-shadow: 3px 3px 12px rgba(0, 0, 0, 0.7);
    background-color: #fff;
    width: 70px;
    height: 70px;
    text-align: center;
    border-radius: 50px;
    color: #666;
    font-size: 30px;
    line-height: 67px;
}

header{
    width: 100%;
    box-shadow: 0px 0px 13px rgba(0, 0, 0, 0.2);
}

.header_wrap{
    width: 1050px;
    height: 160px;
    margin: 0px auto;
    overflow: hidden;
    position: relative;
    
}

.top{
    width: 100%;
    height: 40px;
    background-color: #5f0080;
}


.logo img{
    top: 50px;
    left: 10px;
    width: 80px;
    position: absolute;
}

.login_link{
    text-align: right;
    margin: 0px auto;
    width: 1050px;
    height: 40px;
    font-size: 11px;
    color: #333;
    font-weight: 100;
}

.login_link :any-link{
    font-weight: 400;
    margin: 0px 10px;
    text-decoration: none;
    font-size: 12px;
    line-height: 33px;
    color: #333;
}

.search_box{
    width: 400px;
    height: 45px;
    margin: 0px auto;
    margin-top: 10px;
    position: relative;
}

.search_box input{
    width: 100%;
    height: 100%;
    border: #5f0080 1px solid;
    font-weight: 500;
    border-radius: 7px;
    font-size: 15px;
    text-indent: 12px;
}

.search_box img{
    position: absolute;
    top: 5px;
    right: 5px;
}

.icon_box{
    position: absolute;
    width: 150px;
    right: 50px;
    top: 50px;
}
.icon_box ul{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-inline-start: 0px;
}
.icon_box ul li{
    width: 36px;
    height: 36px;
}
.icon_box ul li img{
    width: 100%;
    height: 100%;
}

.page_info{
    position: absolute;
    top: 55px;
    left: 100px;
}

.page_info p{
    color: #ccc;
    font-size: 12px;
}

.page_info p span{
    font-size: 18px;
    margin: 0px 10px;
}

.page_info p span:nth-child(1){
    margin: 0px 10px;
    color: #5f0080;
    font-weight: 500;
}

ol,li,ul{
    list-style: none;
}
.p_title{
    text-align: center;
    margin-top: 70px;
    font-size: 28px;
    font-weight: 500;
    color: #333;
}
.product_list{
    margin: 0px auto;
    margin-top: 40px;
    width: 1050px;
    display: flex;
    justify-content: space-around;
    padding-inline-start: 0px;
}

.product_list li{
    position: relative;
    width: 249px;
    height: 434px;
    /* border:  1px solid #f00; */
}

.img_contain{
    width: 249px;
    height: 320px;
    overflow: hidden;
}

.img_contain img{
    height: 320px;
    transition: .3s;
}

.img_contain img:hover{
    height: 320px;
    transform: scale(1.05);
}

.product_list li p{
    text-indent: 0px;
}

.p_name{
    margin-top: 7px;
    font-size: 16px;
    color: #333;
}

.price{
    margin-top: 7px;
    font-weight: bold;
}
.discount{
    margin-right: 7px;
    color: red;
}
.p_origin{
    margin-top: 10px;
    color: #999;
    text-decoration: line-through;
    font-size: 14px;
}

.icon{
    font-family: '나눔 고딕', sans-serif;
    box-sizing: border-box;
    text-indent: 0px !important;
    /* background-color: #f00; */
    background-color: #fff;
    position: absolute;
    font-family: bold;
    width: 45px;
    height: 45px;
    border-radius: 40px;
    text-align: center;
    line-height: 45px;
    font-size: 30px;
    color: #ccc;
    top: 255px;
    right: 10px;
    cursor: pointer;
    transition: .2s;
}

.icon:hover{
    background-color: #eee;
    color: rgb(216, 135, 135);
}


#slide-wrapper{
    width: 100%;
    margin: 0px auto;
    position: relative;
    overflow: hidden;
}

#slide{
    width: 500vw;
    height: 100%;
    margin: 0px auto;
    display: flex;
    justify-content: space-around;
    /* background-color: #ccc; */
    list-style: none;
    padding-inline-start: 0px;
    position: relative;
    transition: .3s all;
    margin-block-end: 0px;
    margin-block-start: 0px;
}

#slide li{
    height: 100%;
    width: 100vw;
}

#slide li img{
    height: 100%;
    width: 100%;
}
.btn_group{
position: absolute;
top: 0px;
left: 0px;
width: 100%;
height: 100%;
pointer-events: none;
}

.btn_inner{
    height: 100%;
    width: 1050px;
    margin: 0px auto;
    position: relative;
}

#prev{
    pointer-events: all;
    background-color: rgba(0, 0, 0, .5);
    height: 50px;
    width: 50px;
    position: absolute;
    left: 0px;
    border: none;
    top: 175px;
    cursor: pointer;
    color: #fff;
    border-radius: 50px;
    box-sizing: border-box;
    text-align: center;
    font-size: 30px;
}
#next{
    pointer-events: all;
    background-color: rgba(0, 0, 0, .5);
    height: 50px;
    width: 50px;
    position: absolute;
    right: 0px;
    border: none;
    top: 175px;
    cursor: pointer;
    color: #fff;
    border-radius: 50px;
    box-sizing: border-box;
    text-align: center;
    font-size: 30px;
}



#banner02{
    margin: 0px auto;
    width: 1050px;
}

#banner02 img{
    width: 100%;
}

.menu{
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 40px;
    /* background-color: #fff; */
    display: flex;
    justify-content: center;
    padding-inline-start: 0px;
}

.menu li{
    margin: 0px 50px;
}

.menu li>:any-link{
    color: #333;
    text-decoration: none;
    font-weight: 500;
    font-size: 16px;
}

.menu li>:any-link:hover{
    color: #5f0080;
    text-decoration: underline;
}

.notfound{
    height: 500px;
    width: 1100px;
    margin: 0px auto;
    position: relative;
    overflow: hidden;
}

.notfound img{
    width: 100px;
}

.n_img{
    position: absolute;
    top: 50px;
    left: 50%;
    transform: translateX(-50%);
    margin: 0px auto;
}


.n_info{
    margin-top: 170px;
    text-align: center;
    color: #666;
    font-size: 18px;
    line-height: 25px;
}

.n_info span{
    font-size: 14px;
    line-height: 10px;
    color: #999;
}

.n_back{
    margin: 0px auto;
    margin-top: 40px;
    margin-bottom: 140px;
    width: 200px;
    height: 50px;
}

.n_back input{
    border-radius: 5px;
    width: 100%;
    height: 100%;
    background-color: #5f0080;
    border: none;
    color: #fff;
    font-size: 16px;
}

footer{
    width: 100%;
    border-top: 1px solid #eee;
    height: 300px;
}

.footer_wrap{
    width: 1050px;
    height: 100%;
    margin: 0px auto;
    position: relative;
}

.txt_01{
    position: absolute;
    top: 30px;
    left: 0px;
}

.txt_01 p:nth-child(1){
    font-size: 20px;
    line-height: 40px;
}
.txt_01 p:nth-child(2){
    font-size: 28px;
    font-weight: bold;
    line-height: 40px;
}
.txt_01 p:nth-child(2) span{
    margin-left: 10px;
    font-size: 16px;
    font-weight: 400;
    line-height: 40px;
}

.txt_02{
    position: absolute;
    top: 30px;
    right: 0px;
    font-size: 12px;
    color: #666;
}
.txt_02 ul{
    font-size: 14px;
    display: flex;
    color: #333;
}
.txt_02 ul li{
    margin-right: 13px;
}
.txt_02 p{
    margin-top: 30px;
}
.pur{
    color: #5f0080;
}