.login_title{
    width: 340px;
    margin: 0px auto;
    text-align: center;
    height: 30px;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 30px;
    margin-top: 80px;
}
.input{
    width: 340px;
    height: 50px;
    margin: 0px auto;
    margin-bottom: 10px;
    display: block;
}

.input>input{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 5px;
    text-indent: 10px;
    font-size: 14px;
}

.find{
    width: 340px;
    margin: 0px auto;
}

.find>p{
    text-align: right;
    font-size: 13px;
    color: #333;
}

.login_btn{
    width: 340px;
    height: 50px;
    border-radius: 5px;
    border: none;
    background-color: #5f0080;
    color: #fff;
    margin: 0px auto;
    margin-top: 30px;
    display: block;
    font-size: 16px;
}

.signin_btn{
    width: 340px;
    height: 50px;
    background-color: #fff;
    border: 1px solid #5f0080;
    border-radius: 5px;
    text-align: center;
    color: #5f0080;
    line-height: 50px;
    font-weight: bold;
    box-sizing: border-box;
    margin: 0px auto;
    margin-top: 10px;
    margin-bottom: 70px;
    font-size: 16px;
}

.typebox{
    width: 340px;
    height: 117px;
    border-radius: 5px;
    box-sizing: border-box;
    margin: 0px auto;
    margin-top: 70px;
    margin-bottom: 150px;
    font-size: 16px;
}

.typebox p{
    box-sizing: border-box;
    border-bottom: 2px solid #5f0080;
    font-weight: 600;
    color: #5f0080;
    width: 320px;
    margin: 0px auto;
    margin-top: 10px;
    padding: 10px;
}